import * as React from 'react'
import {useRef, useState} from 'react'
import AppBar from '@mui/material/AppBar'
import Box from '@mui/material/Box'
import Toolbar from '@mui/material/Toolbar'
import IconButton from '@mui/material/IconButton'
import Typography from '@mui/material/Typography'
import Menu from '@mui/material/Menu'
import MenuIcon from '@mui/icons-material/Menu'
import Container from '@mui/material/Container'
import Button from '@mui/material/Button'
import MenuItem from '@mui/material/MenuItem'
import {useNavigate} from "react-router-dom"
import InstagramIcon from "@mui/icons-material/Instagram"
import FacebookIcon from "@mui/icons-material/Facebook"
import CloseIcon from '@mui/icons-material/Close';
import {Slide, useMediaQuery} from "@mui/material";
import {useIsInViewport} from "../hooks/useIsInViewport";
import HorizontalNavBar from "./HorizontalNavBar";

const pages = [
    {name: ' ', path: '/about'},
    {name: 'Lookbook', path: '/lookbook'},
    {name: '', path: '/masters'},
    {name: '', path: '/services'},
    {name: '', path: '/contacts'},
];

export const NavBar = () => {

    const [isMenuOpen, setIsMenuOpen] = useState(false)
    const [touchStartPosition, setTouchStartPosition] = useState(0)
    const [prevTranslatePosition, setPrevTranslatePosition] = useState(0)
    const [swipeDirection, setSwipeDirection] = useState(0)

    const menuRef = useRef(null)
    const isMenuInViewPort = useIsInViewport(menuRef)
    const isThousandWidth = useMediaQuery('(min-width:1000px)')

    const menuPaperElement = document.querySelector('.MuiMenu-paper')

    const [anchorElNav, setAnchorElNav] = React.useState(null);
    let prevNavSwipePositionX = 0
    let prevNavSwipePositionY = 0
    const navigate = useNavigate()

    const handleOpenNavMenu = (event) => {
        const touch = event.touches && event.touches[0]
        if (prevNavSwipePositionX && event && touch.clientX < prevNavSwipePositionX - 15) {
            setAnchorElNav(event.currentTarget);
            setIsMenuOpen(true)
        }
        if (!isMenuOpen && event.type !== 'touchmove') {
            setAnchorElNav(event.currentTarget);
            setIsMenuOpen(true)
        }
        prevNavSwipePositionY = touch && touch.clientY
        prevNavSwipePositionX = touch && touch.clientX
    };

    const handleCloseNavMenu = () => {
        setAnchorElNav(null);
        setIsMenuOpen(false)
    };

    const scroll = (id) => {
        const section = document.getElementById(`${id}`);
        let position = section.offsetTop
        if (id === 'lookbook') {
            position = position - (isThousandWidth ? section.offsetHeight - 70 : section.offsetHeight) / 100 * 30
        }
        window.scrollTo({top: isThousandWidth ? position - 70 : position, behavior: 'smooth'});
    };

    const handleNavClick = (path) => {
        handleCloseNavMenu()
        setTimeout(() => {
            switch (path) {
                case '/about':
                    scroll('about')
                    break
                case '/lookbook':
                    scroll('lookbook')
                    break
                case '/services':
                    scroll('services')
                    break
                case '/masters':
                    scroll('masters')
                    break
                case '/contacts':
                    scroll('contacts')
                    break
            }
        }, 100)
    }

    const openInNewTab = () => {
        window.open('https://instagram.com/kappers_ua?igshid=MzRlODBiNWFlZA==', '_blank', 'noopener,noreferrer');
    };

    const handleTouchStart = (event) => {
        if (isMenuOpen) {
            setTouchStartPosition(event.touches[0].clientX)
        }
    }
    const handleTouchMove = (event) => {
        const translatePosition = touchStartPosition - event.touches[0].clientX
        if (prevTranslatePosition > translatePosition) {
            setSwipeDirection(-1)
        } else if (prevTranslatePosition < translatePosition) {
            setSwipeDirection(1)
        }
        const computedTransform = touchStartPosition > translatePosition ? -translatePosition : +translatePosition
        if (touchStartPosition <= event.touches[0].clientX) {
            menuPaperElement.style.transform = `translateX(${computedTransform}px)`
        }
        setPrevTranslatePosition(translatePosition)
    }

    const handleTouchEnd = () => {
        if (swipeDirection >= 0) {
            menuPaperElement.style.transform = `translateX(0)`
        } else {
            handleCloseNavMenu()
        }
    }

    return (
        <>
            <HorizontalNavBar isMenuInViewPort={isMenuInViewPort}/>
            <Box
                onTouchMove={handleOpenNavMenu}
                sx={{
                    position: {xs: 'fixed', sm: 'fixed', md: 'absolute'},
                    top: 0,
                    right: 0,
                    width: '15%',
                    height: '100%',
                    display: {sm: 'block', md: 'none', lg: 'none'},
                    zIndex: 11
                }}
            >
            </Box>
            <AppBar
                color={'transparent'}
                sx={{
                    mt: 2,
                    boxShadow: 'none',
                    width: {xl: '25dvw', lg: '30dvw'},
                    left: 0,
                    position: {xs: 'fixed', sm: 'fixed', md: 'absolute'}
                }}
            >
                <Container maxWidth={false}>
                    <Toolbar
                        disableGutters
                    >
                        <Typography
                            variant="h3"
                            component="a"
                            onClick={() => navigate('/')}
                            mr={2}
                            mb={20}
                            fontSize={"xxx-large"}
                            fontWeight={700}
                            color={'text.primary'}
                            sx={{
                                display: {xs: 'none', md: 'flex'},
                                flexGrow: 1,
                                textAlign: 'center'
                            }}
                        >
                            <svg
                                xmlns="http://www.w3.org/2000/svg"
                                width="69.3048mm"
                                height="22.0137mm"
                                version="1.1"
                                viewBox="0 0 6487 2060"
                                fill={'#fff'}
                            >
                                <defs>
                                </defs>
                                <g id="_x0020_1">
                                    <metadata id="CorelCorpID_0Corel-Layer"/>
                                    <path
                                        className="fil0"
                                        d="M2118 1964l-2102 -1c-9,0 -16,-7 -16,-16l0 0 0 -1931c0,-9 7,-16 16,-16l6455 0c9,0 16,7 16,16l0 1927c0,9 -7,16 -16,16l-1 0 -2291 5 0 -32 2276 -4 0 -1897 -6424 0 0 1901 2087 1 0 31z"
                                    />
                                    <path
                                        className="fil0"
                                        d="M5146 732c-94,0 -170,76 -170,170 0,94 79,174 173,174l242 1c0,0 5,0 6,0 24,0 44,21 44,46 0,25 -20,45 -44,45 -6,0 -25,1 -28,1l-53 -1 -340 0 34 116 388 0c92,-1 170,-65 170,-163 0,-55 0,-111 0,-166l-340 0 -53 1c-28,0 -50,-23 -50,-51 0,-28 22,-51 50,-51l359 0 34 -122 -422 0zm-2190 0c0,387 1,774 1,1161l45 40 45 -40 108 -556c0,0 -19,-57 -38,-110l0 -151c4,1 270,1 270,1 94,0 161,-81 161,-175 0,-80 -54,-146 -128,-165l23 -79 -119 0 -54 74 -314 0zm-2037 0l0 552 161 0 0 -72 108 -117 208 189 148 0 -242 -312 221 -240 -216 0 -227 283 0 -283 -161 0zm991 132l78 190 -156 0 78 -190zm-128 313l257 0 44 108 145 -1 -196 -552c-81,0 -162,0 -243,0l-196 552 144 0 45 -107zm515 -445l0 551 161 0 0 -207c4,0 270,0 270,0 94,0 161,-80 161,-174 0,-94 -76,-170 -170,-170l-422 0zm161 121l231 0c28,0 51,23 51,51 0,28 -23,50 -51,50l-231 0 0 -101zm544 294c0,-19 16,-34 35,-34 19,0 34,15 34,34 0,19 -15,35 -34,35 -19,0 -35,-16 -35,-35zm115 -293l232 0c28,0 50,22 50,50 0,28 -23,51 -51,51l-231 -1 0 -100zm505 -122l0 552 591 0 -33 -120 -398 0 0 -98 0 -4 284 0 20 -105 -303 0 -1 0 0 -101 398 0 33 -124 -591 0zm1002 344l152 208 127 0 -89 -222c58,-28 95,-90 95,-160 0,-94 -76,-170 -170,-170l-422 0 0 552 161 0 0 -208c2,0 76,0 146,0zm-146 -223l232 0c28,0 50,23 50,51 0,28 -23,50 -51,50l-231 0 0 -101z"
                                    />
                                    <path
                                        className="fil0"
                                        d="M2408 2044c-15,10 -34,16 -56,16 -29,0 -53,-10 -69,-27 -17,-18 -27,-44 -27,-73 0,-30 11,-55 27,-73 17,-19 41,-30 68,-30 28,0 51,10 67,27 16,18 25,43 25,72l0 11 0 4 -5 0 -143 0c1,17 6,30 15,40l0 0c10,10 25,15 43,15 12,0 24,-3 33,-10 9,-6 15,-15 18,-27l1 -4 5 1 27 6 5 1 -2 5c-5,19 -16,35 -32,46zm-96 -137l0 0c-8,8 -14,19 -16,32l109 0c-2,-14 -8,-25 -16,-33 -10,-9 -23,-14 -38,-14 -15,0 -29,5 -39,15zm332 136c-14,11 -33,17 -55,17 -22,0 -39,-5 -52,-15 -14,-11 -22,-26 -24,-44l-1 -4 4 -1 26 -7 5 -1 0 5c1,11 6,20 12,25 8,6 18,9 31,9 13,0 22,-3 29,-8 6,-5 10,-12 10,-20 0,-20 -22,-24 -45,-29 -31,-6 -66,-13 -66,-57 0,-17 8,-31 20,-40 13,-10 31,-16 52,-16 17,0 35,6 48,16 12,9 21,23 23,40l1 4 -4 1 -24 7 -6 2 0 -6c-1,-9 -6,-17 -12,-22l-1 0c-6,-5 -16,-8 -27,-8 -11,0 -19,2 -25,6 -5,4 -8,10 -8,16 0,17 19,21 41,25 32,6 69,13 69,60 0,18 -8,34 -21,45zm211 13l-26 0c-19,0 -35,-5 -45,-15l0 0c-10,-9 -16,-23 -16,-42l0 -104 -30 0 -5 0 0 -5 0 -23 0 -5 5 0 32 0 0 -49 0 -5 4 0 27 0 4 0 0 5 0 49 52 0 5 0 0 5 0 23 0 5 -5 0 -52 0 0 103c0,9 2,15 5,19 4,3 10,5 18,5l27 0 5 0 0 5 0 24 0 5 -5 0zm160 -3c-5,5 -11,7 -18,7 -6,0 -13,-2 -17,-7 -5,-4 -8,-10 -8,-17 0,-7 3,-13 8,-17 4,-5 11,-8 17,-8 7,0 13,3 18,8 4,4 7,10 7,17 0,7 -3,13 -7,17zm282 3l-151 0 -5 0 0 -5 0 -36 0 -2 2 -2 99 -103c6,-5 10,-11 14,-18 3,-6 5,-13 5,-21 0,-12 -4,-22 -12,-29 -7,-6 -17,-10 -30,-10 -12,0 -23,4 -30,11 -7,8 -12,18 -12,30l0 4 -4 0 -27 0 -5 0 0 -4c0,-20 8,-41 23,-56 13,-13 32,-22 56,-22 23,0 43,9 56,21 15,14 24,34 24,54 0,14 -3,25 -8,34 -5,10 -11,19 -19,27l0 0 -91 91 115 0 5 0 0 5 0 26 0 5 -5 0zm240 -20c-17,16 -39,24 -61,24 -23,0 -45,-8 -61,-24 -15,-14 -24,-34 -24,-59l0 -100c0,-25 9,-45 24,-59 16,-16 38,-25 61,-25 22,0 44,9 61,25 14,14 24,34 24,59l0 100c0,25 -10,45 -24,59zm-95 -26c8,9 20,14 34,14 13,0 25,-5 33,-14 9,-8 14,-21 14,-35l0 -96c0,-14 -5,-27 -14,-35 -8,-9 -20,-14 -33,-14 -14,0 -26,5 -34,14 -8,8 -14,21 -14,35l0 96c0,14 5,27 14,35zm364 46l-151 0 -5 0 0 -5 0 -36 0 -2 1 -2 100 -103c5,-5 10,-11 13,-18 3,-6 5,-13 5,-21 0,-12 -4,-22 -11,-29 -7,-6 -18,-10 -31,-10 -12,0 -22,4 -29,11 -8,8 -12,18 -12,30l0 4 -5 0 -27 0 -4 0 0 -4c0,-20 7,-41 23,-56 13,-13 31,-22 55,-22 24,0 43,9 57,21 15,14 23,34 23,54 0,14 -3,25 -7,34 -5,10 -12,19 -19,27l-1 0 -91 91 116 0 4 0 0 5 0 26 0 5 -4 0zm233 -19c-15,14 -36,23 -62,23 -24,0 -43,-9 -56,-21 -16,-16 -24,-37 -24,-56l0 -5 5 0 27 0 5 0 0 5c0,11 5,22 12,29 8,8 18,12 31,12 14,0 26,-5 34,-13 8,-9 13,-21 13,-35 0,-13 -5,-26 -13,-34 -8,-8 -20,-13 -35,-13l-25 0 -5 0 0 -5 0 -32 0 -2 2 -1 68 -55 -108 0 -5 0 0 -5 0 -26 0 -5 5 0 144 0 5 0 0 5 0 38 0 2 -2 2 -63 50c20,2 36,10 47,22 17,17 24,40 24,58 0,24 -8,46 -24,62z"
                                    />
                                </g>
                            </svg>
                        </Typography>
                        <Box
                            sx={{display: {xs: 'flex', md: 'none'}}}
                        >
                            <IconButton
                                size="large"
                                onClick={handleOpenNavMenu}
                                color={"primary"}
                                sx={{
                                    borderRadius: '50%',
                                    background: 'black'
                                }}
                            >
                                <MenuIcon/>
                            </IconButton>
                            <Menu
                                id="menu-appbar"
                                anchorEl={anchorElNav}
                                anchorOrigin={{
                                    vertical: 'top',
                                    horizontal: 'left',
                                }}
                                keepMounted
                                transformOrigin={{
                                    vertical: 'top',
                                    horizontal: 'right',
                                }}
                                TransitionComponent={Slide}
                                TransitionProps={{direction: 'left'}}
                                marginThreshold={0}
                                open={Boolean(anchorElNav)}
                                onClose={handleCloseNavMenu}
                                sx={{
                                    display: {xs: 'block', md: 'none'},
                                    right: 0,
                                    left: 'initial!important',
                                }}
                                PaperProps={{
                                    style: {
                                        width: '70%',
                                        maxWidth: "100%",
                                        backgroundColor: 'rgb(23,22,22)',
                                        right: 0,
                                        left: 'unset!important',
                                        height: '100dvh',
                                        maxHeight: '100dvh'
                                    }
                                }}
                            >
                                <Box
                                    onTouchStart={(event) => handleTouchStart(event)}
                                    onTouchMove={(event) => handleTouchMove(event)}
                                    onTouchEnd={handleTouchEnd}
                                    sx={{
                                        width: '100%',
                                        height: '100%'
                                    }}
                                >
                                    <Container
                                        disableGutters={true}
                                        sx={{
                                            width: '100%',
                                            display: 'flex',
                                            justifyContent: 'end'
                                        }}
                                    >
                                        <Button
                                            onClick={handleCloseNavMenu}
                                            sx={{
                                                padding: '7px 7px 0 0',
                                                minWidth: '33px'
                                            }}
                                        >
                                            <CloseIcon fontSize={'large'}/>
                                        </Button>
                                    </Container>
                                    <MenuItem
                                        onClick={() => handleNavClick('/')}
                                    >
                                        <Typography
                                            fontSize={"x-large"}
                                            fontWeight={600}
                                            pt={0}
                                            mb={2}
                                            onClick={() => {
                                                window.scrollTo({top: 0, behavior: 'smooth'})
                                            }}
                                        >
                                            KAPPERS
                                        </Typography>
                                    </MenuItem>
                                    {pages.map((page) => (
                                        <MenuItem
                                            key={page.name}
                                            onClick={() => handleNavClick(page.path)}
                                        >
                                            <Typography
                                                fontSize={"x-large"}
                                                fontWeight={600}
                                            >
                                                {page.name}
                                            </Typography>
                                        </MenuItem>
                                    ))}
                                    <Container
                                        maxWidth={false}
                                        sx={{
                                            display: 'flex',
                                            flexDirection: 'column',
                                            justifyContent: 'start',
                                            textAlign: 'center',
                                            position: 'absolute',
                                            bottom: '10%',
                                            left: 0
                                        }}
                                    >
                                        <Typography
                                            fontSize={'small'}
                                            sx={{
                                                padding: '0 16px',
                                                textAlign: 'start',
                                                mb: 1
                                            }}
                                        >
                                             
                                        </Typography>
                                        <Typography
                                            fontSize={'small'}
                                            sx={{
                                                padding: '0 16px',
                                                textAlign: 'start',
                                                mb: 2
                                            }}
                                        >
                                            <a
                                                href="tel:+38 067 86 42 108"
                                                style={{textDecoration: 'none', color: 'inherit'}}
                                            >
                                                +38 093 56 73 236
                                            </a>
                                        </Typography>
                                        <Typography
                                            fontSize={'small'}
                                            sx={{
                                                padding: '0 16px',
                                                textAlign: 'start'
                                            }}
                                        >
                                            . ,   1
                                        </Typography>
                                    </Container>
                                    <Container
                                        maxWidth={false}
                                        sx={{
                                            display: 'flex',
                                            justifyContent: 'start',
                                            textAlign: 'center',
                                            position: 'absolute',
                                            bottom: '1%',
                                            left: 0
                                        }}
                                    >
                                        <Button
                                            sx={{minWidth: 'initial'}}
                                            onClick={openInNewTab}
                                        >
                                            <InstagramIcon
                                                fontSize={'medium'}
                                                sx={{margin: '0 10px', padding: 0}}
                                            />
                                        </Button>
                                    </Container>
                                </Box>
                            </Menu>
                        </Box>
                        <Box
                            sx={{display: {xs: 'none', md: 'flex', marginLeft: '17%', marginTop: '15vh'}}}
                            ref={menuRef}
                        >
                            {pages.map((page) => (
                                <Button
                                    key={page.name}
                                    onClick={() => handleNavClick(page.path)}
                                    sx={{
                                        my: 2,
                                        color: 'white',
                                        display: 'block',
                                        fontSize: 20,
                                        fontWeight: 700,
                                        textAlign: 'left',
                                        textTransform: 'initial'
                                    }}
                                >
                                    {page.name}
                                </Button>
                            ))}
                        </Box>
                    </Toolbar>
                </Container>
            </AppBar>
        </>
    )
}
