import React from 'react';
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import Box from "@mui/material/Box";
import {Paper} from "@mui/material";

const AboutScreen = () => {
    return (
        <Paper
            sx={{
                padding: {xs: '2% 0 50%', sm: '2% 5% 50%', md: '6% 2% 27%', lg: '6% 18% 27%'}
            }}
        >
            <Box
                id={'about'}
                sx={{
                    display: 'flex',
                    flexDirection: {xs: 'column', sm: 'column', md: 'row', lg: 'row'},
                    padding: {xs: 4, sm: 4},
                    scrollBehavior: 'smooth'
                }}
            >
                <Container
                    sx={{
                        width: {xs: '100%', sm: '100%', md: '60%', lg: '55%'}
                    }}
                >
                    <Typography
                        mb={4}
                        sx={{
                            fontSize: {xs: 30, sm: 30, md: 50, lg: 50},
                            fontWeight: 800,
                            color: 'rgb(152, 107, 78)'
                        }}
                        className={'about'}
                    >
                          
                    </Typography>
                    <Typography
                        mb={4}
                        sx={{
                            fontSize: {xs: 16, sm: 16, md: 20, lg: 20},
                            color: 'black'
                        }}
                    >
                               .
                    </Typography>
                    <Typography
                        mb={4}
                        sx={{
                            fontSize: {xs: 16, sm: 16, md: 20, lg: 20},
                            color: 'black'
                        }}
                    >
                              : , , ,        .
                    </Typography>
                    <Typography
                        mb={2}
                        sx={{
                            fontSize: {xs: 16, sm: 16, md: 20, lg: 20},
                            color: 'black'
                        }}
                    >
                               ,          .
                    </Typography>
                </Container>
                <Container
                    sx={{
                        width: {xs: '100%', sm: '100%', md: '35%', lg: '35%'},
                        pt: {xs: 0, sm: 0, md: 3, lg: 3}
                    }}
                >
                    <Typography
                        mb={4}
                        sx={{
                            fontSize: {xs: 16, sm: 16, md: 18, lg: 18},
                            color: '#4d4d4d'
                        }}
                    >
                                 .   ,         .
                    </Typography>
                    <Typography
                        sx={{
                            fontSize: {xs: 16, sm: 16, md: 18, lg: 18},
                            color: '#4d4d4d'
                        }}
                    >
                          '  Kapper   ,    ,       .                .
                    </Typography>
                </Container>
            </Box>
        </Paper>
    );
};

export default AboutScreen;
